.App {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  height: 100vh;
  background-color: rgb(236, 236, 236);
}

.tweet-container {
  background-color: #ffffff;
  border-radius: 10px;
  border: solid 1px rgba(1, 1, 1, 0.419);
  padding: 15px;
}

.tweet-header {
  display: flex;
}

.tweet-text {
  display: flex;
  margin: 0 5px 0 0
}

.tweet-main {
  position: relative;
  margin-left: 55px;
  bottom: 15px;
}

.author-name {
  font-weight: bold;
  margin: 0 5px;
}

.author-nickname {
  color: rgba(0, 0, 0, 0.500);
  margin-right: 5px;
}

.tweet-date {
  color: rgba(0, 0, 0, 0.500);
}

.author-avatar {
  height: 50px;
  width: 50px;
  border-radius: 50px;
}

.tweet-photo {
  display: flex;
  border-radius: 10px;
  height: 300px;
  max-width: 100%;
  margin: 10px 0;
  box-sizing: border-box;
  border: solid 1px rgba(1, 1, 1, 0.419);
}

.tweet-footer {
  display: flex;
  justify-content: space-between;
}

.tweet-footer span {
  display: flex;
  align-items: center;
}

.tweet-icons,
.tweet-icons-like {
  height: 25px;
  width: 25px;
  cursor: pointer;
  margin: 0 10px;
}

.tweet-icons:hover {
  fill: rgb(22, 159, 245);
}

.tweet-icons-like:hover {
  fill: rgb(245, 22, 22);
}